body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#00695f;color:white}h1,h2,h3,h4,h5{text-align:center;font-family:'serenity', sans-serif;font-weight:300;font-style:normal}

h1,h2,h3,h4,h5,.title{text-align:center;font-family:'serenity', sans-serif;font-weight:300;font-style:normal;margin:0 auto;font-size:300%}.subtitle{font-size:200%;margin:0 auto;margin-right:1em;margin-top:0.5em;text-align:center}#profile{margin:0 auto;width:500px;height:auto}div.MuiGrid-root.MuiGrid-item.MuiGrid-grid-xs-6{max-width:-webkit-fit-content;max-width:-moz-fit-content;max-width:fit-content;margin:0}#paper{background:#1C3A3E;color:#00695f;width:50%;padding:0.25em 2em 2em 2em}.logo{text-align:center;color:#6bcfc2;font-family:'fave-script-pro', sans-serif;font-weight:400;font-style:normal;font-size:700%;padding:0;margin:0;padding-top:0.25em}.title{text-align:center;color:#00695f;font-family:'fave-script-pro', sans-serif;font-weight:400;font-style:normal;font-size:700%;padding:0;margin:0 auto;padding-top:0.25em}.navItem{-webkit-text-decoration-line:none;text-decoration-line:none;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:white;margin:auto 0;text-align:center}.navItem .navbar-item :hover{color:#1C3A3E}.navItem .navbar-item i.icon{font-size:1em}#contact{margin:0 auto;background-color:#1C3A3E}#contact .form{margin:0 auto;padding-top:2.5em;padding-bottom:3em;width:60%}#contact .form .contactInput{width:100%;margin:0 auto;margin-bottom:1em}label.MuiFormLabel-root{color:#009688}label.MuiFormLabel-root.Mui-focused{color:#6bcfc2}.Mui-focused.MuiInputBase-formControl.Mui-focused{color:#009688;border-bottom:#009688 1px solid}.MuiInputBase-root.MuiFilledInput-root.MuiFilledInput-underline.MuiInputBase-formControl.MuiFilledInput-underline:after{transition:transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;transform:scaleX(0);border-bottom:#009688 1px solid}.resumeContainer{margin:0 auto}.resumeContainer .resumeContent{margin:0 auto}#skills{list-style-type:none;width:100%;padding:1em;background-color:#EB8989}#skills li .chip{margin:0.25em}#timeline{margin:auto 0;padding:1em}@media (max-width: 1023px){#profile{width:80%}#contact .form{width:80%}#skills{padding:0;width:100%}#skills li{margin:0 auto;display:inline-block}}

